<template>
  <Card class="card">
     <Breadcrumb>
        <BreadcrumbItem>港口管理</BreadcrumbItem>
        <BreadcrumbItem>港口列表</BreadcrumbItem>
    </Breadcrumb>
      <div class='box-container'>
         <div style="margin:20px 0 40px 0">
               <Input placeholder="请输入港口" style='width:220px' v-model="name" @keyup.enter.native="search()" /> <Button type="primary" @click="search()">搜索</Button>
         </div>
         <Table border :columns="columns1" :data="data1"></Table>
         <Page :total="total" class="pages"   @on-page-size-change='sizeChange'
         @on-change="changePage" :current="currentPage"  show-elevator show-sizer
         :page-size="size" />
      </div>
  </Card>
</template>
<script>
    export default {
        data () {
            return {
               name:'',
               columns1: [
                     {
                        title: '#',
                        key: 'Index',
                         align:'center',
                         width:80
                    },
                    {
                        title: '港口',
                        key: 'port',
                        align:'center',
                    },
                    {
                        title: '吃水',
                        key: 'water',
                        align:'center',
                    },
                    {
                        title: '港口使用费',
                        key: 'portFee',
                        align:'center',
                    },
                    {
                        title: '船长限制',
                        key: 'shipLongF',
                        align:'center',
                    },
                    {
                        title: '船宽限制',
                        key: 'shipWidthL',
                        align:'center',
                    },
                    {
                        title: '封航开始时间',
                        key: 'starTime',
                        align:'center',
                    },
                    {
                        title: '封航结束时间',
                        key: 'endTime',
                        align:'center',
                    },
                    {
                        title: '封航原因',
                        key: 'reason',
                        align:'center',
                    },
                    {
                        title: '详情',
                        key: 'detail',
                         align:'center',
                         render: (h, params) => {
                           return h('Button', {
                             props:{
                               type:"primary"
                             },
                              style:{
                                display:'inline-block',
                                width:100,
                                background:'red;'
                              },
                              on:{
                                click:()=>{
                                    let routeUrl = this.$router.resolve({
                                      path: "/portCenter/portdetail",
                                      query: {code:params.row.code}
                                });
                                window.open(routeUrl .href, '_blank');

                                }
                              }
                           },'查看')
                       }
                    },
                ],
                data1: [],
                  currentPage:1,
                  size:10,
                  total:20,
            }
        },
         methods:{
          async  portList(){
            let data = {
              currentPage:this.currentPage,
              size:this.size,
            }
               try{

                   let res = await this.$http.portList(data)
                   if(0==res.data.status &&  res.data.result instanceof Array){
                       this.total = res.data.total
                       let result = res.data.result
                       this.data1 = []
                       this.commonFn(result)
                   }
                   else{
                     this.$Notice.error({
                       desc:'暂无数据'
                     })
                     this.data1 = []
                     this.total=0
                   }
              }
              catch(err){
                this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)

              }
          },
        commonFn(result){
              result.forEach((item,index) => {
                  this.data1.push({
                        Index:Number((this.currentPage-1)*this.size)+Number(index)+1,
                        port: item.port,
                        water: item.shipDraught,
                        portFee: item.usageFee,
                        shipLongF: item.shipLength,
                        shipWidthL:item.shipBreadth,
                        starTime: item.startDate,
                        endTime: item.endDate,
                        reason: item.stopReason,
                        id:item.portId,
                        code:item.code,
                  })
                });
        },
        changePage(currentPage){
            this.currentPage = currentPage;
            if(localStorage.searchInfo){
              this.search(currentPage)
            }
            else{
             this.portList()
            }
          },
       sizeChange(size){
            this.size = size
            if(!this.name){
              this.portList()
            }
            else{
              this.search()
            }
          },
           // 查询
        async  search(currentPage){
          if(!this.name){
            this.$Notice.error({
              desc:"请输入港口"
            })
            return false
          }
          currentPage ? this.currentPage = currentPage : this.currentPage =1
             let data = {
                  currentPage: this.currentPage,
                  size:this.size,
                  trem:this.name,
             }
             try{
                  let res = await this.$http.selectPortInfo(data)
                    this.total = res.data.total
                    this.data1 = []
                   if(!res.data.status &&  res.data.result instanceof Array){
                      localStorage.setItem('searchInfo',this.name)
                       this.$Notice.success({
                          desc:'查询成功'
                        })

                      let result = res.data.result
                      this.commonFn(result)
                   }
                   else{

                     this.$Notice.error({
                        desc:'暂无数据'
                      })
                      return false
                   }
             }
             catch(err){
               localStorage.removeItem('searchInfo')
               this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)
             }


          },
        },
        created(){
          this.portList()
        },
       watch:{
            name(){
              if(!this.name){
                 this.portList()
                 localStorage.removeItem('searchInfo')
              }
            }
        },
      destroyed(){
           localStorage.removeItem('searchInfo')
        }
    }
</script>
<style lang="scss" scoped>
.box{
  margin-top: 70px;
}
/deep/  .ivu-page{
  position: relative;
}
</style>



